input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
textarea,
select{
	border: 1px solid $strong-color;
	padding:9px 15px;
	outline: none;
	background-color: white;
	display: inline-block;
	vertical-align: middle;
	border-radius: 0;

	&:active, &:hover, &:focus{
		border-color: $secondary-color;
	}
}

select{
	background: url(../img/select.png) no-repeat right center!important;
	padding-right: 50px;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	
	&::-ms-expand{
		visibility: hidden;
		display: none;
	}
}

.select2-container .select2-choice {
	padding: 5px 15px;
	border-radius: 0;
	border: 1px solid #333;
}

textarea{
	min-height:150px;
	resize: vertical;
}

button{
	overflow: visible; /* fix for IE11 */
}

input[type="submit"]{
	@extend .button;
	border-width: 1px!important;

	&:hover{
		background-color: $secondary-color !important;
		border-color: white !important;
		color: white;
	}
}