// Sidebar
.site-sidebar{
	width: 400px;
	position: fixed;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: #fff;
	z-index: 9999;
	// transform: translateX(400px);
	right: -400px;
	transition: .3s ease;
	overflow: auto;
	padding: 30px;

	.admin-bar &{
		top: 32px;
	}
}
.sidebar-active{ 
	overflow: hidden;

	.site-content{
		filter: blur(3px);
	}

	.site-sidebar{
		// transform: translateX(0);
		right: 0;
		box-shadow: -2px 0 20px rgba(0,0,0,.5);
	}
	.overlay{
		opacity: 1;
		z-index: 1000;
	}
}
.overlay{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(255,255,255,.7);
	opacity: 0;
	z-index: -1;
	transition: .3s ease;
}