/*======================================*
 * Widgets
 * ======================================*/

.widget{
	@include clearfix;
	clear: both;
	position: relative;
	margin-bottom: 30px;
	word-break: break-word;

	&:last-child{
		margin-bottom: 0;
	}

	.widget-title{
		color: $muted-color;
		@include em(12);
		font-weight: 700;
		letter-spacing: 2px;
		text-transform: uppercase;
		margin-top: 0;
		text-align: center;
		word-break: break-word;

	}


	// Wordpress Default Widgets
	
	/* Categories, Archive widget list */
	&.widget_categories , 
	&.widget_pages, 
	&.widget_meta, 
	&.widget_nav_menu,
	&.tokoo-advanced-categories,
	&.widget_product_categories,
	&.widget_layered_nav,
	&.dokan-category-menu{

		ul{ list-style: none }

		li{
			@include clearfix;
			color: $accent-color;
			position: relative;

			&:last-child{border-bottom: none}
			
			a{
				padding: 10px 0;
				border-bottom: none;
				color: $strong-color;
				font-weight: 600;
				word-break: break-word;
				display: block;
				border-bottom: 1px solid #eaeaea;

				&:hover{
					color: $accent-color;
				}
			}

			span{
				right: 0;
				top: 0;
				position: absolute;
				margin-top: 10px;
			}

			ul{ 
				margin-left: 5%;
			}
		}

		select{
			border: 1px solid $secondary-color;
			-webkit-appearance: none;
			-moz-appearance: none;
			appearance: none;
			border-radius: 0;
			padding: 10px;
			width: 100%;
			background: transparent;
			outline: none;

			&:active, &:focus{
				border-color: $accent-color;
			}
		}

	}

	&.widget_pages,&.widget_meta, &.widget_recent_comments, &.widget_recent_entries, &.widget_nav_menu,
	&.dokan-feature-seller-widget{
		ul{
			list-style: none;
		}
		li{
			text-align: left;
			@include clearfix;
			margin-bottom: 20px;
			a{ float: none; }
		}
	}
	
	&.widget_recent_comments,
	&.widget_recent_entries,
	&.widget_archive{
		ul{
			list-style: none;
			li{
				padding: 10px 0;

				&:not(:last-child){
					border-bottom: 1px solid #e3e3e3;
				}
			}
		}

		select{ width: 100% }
	}

	/* RSS Widget */
	&.widget_rss{
		li {
			padding: 10px 0;
			border-bottom: 1px solid $bg-color;
			list-style: none;

			a.rsswidget {
				color: $strong-color;
				padding-bottom: 0;
				border-bottom: none;
				@include em(16);
				line-height: normal;
				word-break: break-word;
			}

			.rss-date {
				color: lighten($body-color,10%);
				font-style: italic;
				@include em(13);
				line-height: normal;
				margin-bottom: 10px;
				display: block;
			}
			.rssSummary {
				@include em(14);
				line-height: 1.8;

			}

			cite {
				@include em(12);
				font-style: italic;
			}
		}

	}

	/* Text Widget */
	&.widget_text{
		word-break: break-word;
		img,input,textarea,select{
			max-width: 100%;
		}
	}

	/* Tag list */
	.tagcloud{
		a{
			display: inline-block;
			padding: 5px 10px;
			margin: 0 2px 5px 0;
			border: 2px solid $accent-color;
			font-size: 1em!important;
			word-break: break-word;
		}
	}

	/* Calendar Widget */
	#wp-calendar{
		width: 100%;
		border-collapse: collapse;
		td,th{
			border: 1px solid $bg-color;
			padding: 10px;
			text-align: center;
		}
		th{
			background: $bg-color;
		}

		caption{
			text-transform: uppercase;
			padding: 10px;
			font-weight: 700;
		}

		#today{
			
			background-color: $accent-color;
			color: white;
			a { color: white }
			
		}
	}

	/* Login Widget */
	&.tokoo-login-form{
		input:not([type="checkbox"]){ width: 100% }
		.remember-me{float: left}
		.login-submit{float: right}

		.forget-link{
			clear: both;
			padding-top: 20px;
			margin-bottom: 0;
			border-top: 1px solid $bg-color;
			text-align: center;
			font-weight: 300;
		}
	}

	/* Search Widget */
	&.widget_search{
		
		label{
			@include clearfix;
		}
		.screen-reader-text{display: none}
		input[type="text"],input[type="search"]{
			display: block;
			vertical-align: middle;
			margin-bottom: 10px;
			border: none;
			box-sizing: border-box!important;
			border-bottom: 1px solid #eaeaea;
			width: 100%;
			outline: none;
			&:focus{
				border-color: $accent-color;
			}
		}
		input[type="submit"]{
			float: right;
		}

	}

	img{
		height: auto;
	}

	&.tokoo-recent-comments-with-avatar{
		.post-list{
			li > a{
				float: left;
				margin-right: 15px;
			}
		}
	}

	.tokoo_themeforest_widget_ul{
		list-style: none;
		margin: 0 -5px 20px;
		@include clearfix;

		li{
			width: 33.3333%;
			float: left;
			padding: 0 5px;

			img{
				width: 100%;
				max-width: 100%;
				height: auto;
			}
		}

		.tokoo_read_more{
			text-align: center;
			font-style: italic;
		}
	}

	&.tokoo-contact-info{
		[class*="contact"]{
			display: block;
		}
	}

	&.widget_product_search{
		@include clearfix;

		.search-field{
			display: block;
			width:100%;
			margin-bottom: 10px;
		}

		input[type="submit"]{
			float: right;
		}
	}

	&.tokoo-mailchimp-subscribe-form {
		p{
			text-align: center;
			font-style: italic;
			@include em(12);
		}
		form {
			display: table;
			width: 100%;
			border-collapse: separate;
			border-spacing: 5px;
	
			p{
				display: table-cell;
				vertical-align: bottom;

				label{ display: none }
			}
		}
	}

	&.tokoo-contact-info{
		text-align: center;
		p{
			font-style: italic;
			@include em(12);
		}
		.contact-phone{
			@include em(24);
		}
	}

	&.tokoo-recent-tweets{
		.twitter-tweets-list{
			list-style-type: none;

			li{
				margin-bottom: 30px;
			}
		}
		.tweet{
			position: relative;
			@include clearfix;

			.tweet-avatar{
				float: left;
				margin-right: 20px;
			}
			.tweet-content{
				display: table;
			}
			.timespan{float: right}
		}
	}

	&.tokoo-testimonials{
		blockquote{ margin: 0; }
	}

}