.mini-cart{
	position: relative;

	&__toggle{
		outline: none;
		i{ margin-right: 5px; }
	}

	.widget_shopping_cart{
		position: absolute;
		right: 0;
		z-index: 10;
		background-color: white;
		padding: 30px;
		width: 300px;
		box-shadow: 0 2px 5px rgba(0,0,0,.1);
		transition: .3s ease;
		transform: scale(0);
		-webkit-transform: scale(0);
		opacity: 0;
		transform-origin: 100% 0;
		-webkit-transform-origin: 100% 0;
		top: 100%;

		&.active{
			-webkit-transform: scale(1);
			transform: scale(1);
			opacity: 1;
		}
	}
}

.woocommerce-result-count{
	display: none;
}
.orderby{
	border-color: transparent transparent $secondary-color;
	@include em(12);
	color: $primary-color;

	&:hover,&:active{
		border-color: transparent transparent $primary-color;
	}
}
.woocommerce-ordering{
	margin-right: 50px;
}
.products-holder{
	@include clearfix;
}
.card-item.product{
	.inner-product > a{display: block}
	.product-detail{
		position: absolute;
		width: 100%;
		padding: 30px 20px 20px;
		background-color: white;
		top: 0;
		left: 0;
		text-align: center;
		
		.onsale{
			position: absolute;
			background-color: #dd645e;
			color: white;
			font-weight: 500;
			text-transform: uppercase;
			@include em(10);
			top: 0;
			padding: 3px 5px;
			width:50px;
			margin: 0 auto;
			left: 0;
			right: 0;
			line-height: normal;
			z-index: 1;

			&.soldout{
				width: 100px!important;
			}
		}
		.product-category{
			@include em(11);
			font-style: italic;
		}

		.product-title{
			margin: 0;
			@include em(13);
		}

		.star-rating{
			float: none;
			margin: 5px auto 0;
			width: 70px;
		}
	}
	.product-price{
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		padding: 20px;
		text-align: center;
		background-color: rgba(255,255,255,.9);

		.price{
			ins{
				text-decoration: none;
				font-weight: 700;
			}
		}
	}

	.featured-image.card-image-bg{
		background-repeat: no-repeat;
		background-position-y: 110px;
		background-color: white;
	}

	.product-action{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(255,255,255,.95);
		opacity: 0;
		transition: .3s ease;
		-webkit-transform: scale(1.2);
		transform: scale(1.2);
		text-transform: uppercase;
		letter-spacing: 2px;
		z-index: 2;

		.block-link{
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
		}

		.plus{
			position: relative;
			width: 33px;
			height: 33px;
			display: block;
			margin: 0 auto 10px;

			&:before{
				@include cover;
				content:" ";
				background-image: url(../img/cart-icon.png);
				background-repeat: no-repeat;
				background-position: 0 0;
				transition: .3s ease;
			}

			span{
				@include cover;

				&:before, &:after{
					@include cover;
					content:" ";
					background-image: url(../img/cart-icon.png);
					background-repeat: no-repeat;
					transition: .3s ease;
					-webkit-transform: scale(0);
					transform: scale(0);
				}
				&:before{
					background-position: 0 -33px;
					opacity: 0;
				}
				&:after{
					background-position: 0 -66px;
				}
			}
		}

		.add_to_cart_button,
		.button.product_type_simple,
		.button.product_type_external,
		.button.product_type_grouped,
		.button.product_type_variable{
			position: absolute;
			width: 150px;
			height: 150px;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			margin: auto;
			text-align: center;
			border: none;
  			font-size: inherit;
  			padding-top: 40px;

			&.loading{
				.plus{
					&:before{
						-webkit-transform: scale(0);
						transform: scale(0);
					}

					span{
						&:before{
							opacity: 1;
							-webkit-transform: scale(1);
							transform: scale(1);
							animation: rotate 1s infinite;
						}
					}
				}
			}

			&.added{
				.plus{
					&:before{
						transform: scale(0);
						-webkit-transform: scale(0);
						opacity: 0;
					}

					span{
						&:before{
							transform: scale(0);
							-webkit-transform: scale(0);
						}
						&:after{
							transform: scale(1);
							-webkit-transform: scale(1);
						}
					}
				}
			}

			&:after {
				background-color: rgba(221, 100, 94,.2);
				content: " ";
				width: 100%;
				height: 100%;
				position: absolute;
				left: 0;;
				top: 0;
				z-index: -1;
				border-radius: 50%;
				transition: .4s cubic-bezier(0.02, 0.54, 0.31, 1.88);
				transform: scale(0);
				-webkit-transform: scale(0);
				opacity: 0;
				will-change: transform;
			}

			&:hover{
				background-color: transparent;
				color: inherit;
				box-shadow: none;

				&:after{
					transform: scale(1);
					-webkit-transform: scale(1);
					opacity: 1;
				}
			}
		}

		a.added_to_cart.wc-forward {
			display: table;
			margin: 0 auto;
			background-color: $secondary-color;
			color: white;
			font-weight: 700;
			font-size: 10px;
			padding: 5px 10px;

			&:hover{
				background-color: $primary-color;
			}
		}
		
		> .yith-wcqv-button{
			display: none;
		}

		.bottom-action{
			@include clearfix;
			position: absolute;
			bottom: 0;
			width: 100%;
			padding: 0 20px 30px;
			text-align: center;
			display: table;
			
			.yith-wcqv-button{
				position: absolute;
				left: 30px;
				top: 0;


				.tooltip{
					position: absolute;
					background-color: rgba(0,0,0,.7);
					padding: 3px 5px;
					color: white;
					bottom: 100%;
					margin-bottom: 10px;
					left: -5px;
					border-radius: 3px;
					@include em(12);
					text-transform: initial;
					letter-spacing: normal;
					min-width: 150px;
					font-family: $secondary-font;
					opacity: 0;
					transform: scale(0);
					-webkit-transform: scale(0);
					transform-origin: 0 100%;
					-webkit-transform-origin: 0 100%;
					transition: .3s ease;

					&:after{
						content:" ";
						width: 0;
						border-style: solid;
						border-width: 5px;
						border-color: rgba(0,0,0,.7) transparent transparent transparent;
						position: absolute;
						top: 100%;
						left: 10px;
					}
				}
				&:hover .tooltip{
					opacity: 1;
					transform: scale(1);
					-webkit-transform: scale(1);
				}
			}

			.yith-wcwl-add-to-wishlist{
				position: absolute;
				right: 30px;
				top:0;
				margin-top: 0;
				width: 16px;
				height: 16px;

				.fa{  margin-right: 0;}

				.yith-wcwl-add-button{
					position: relative;

					.ajax-loading{
						position: absolute;
						left: 0;
						top: 0;
						margin-left: 0;
					}
				}
				.yith-wcwl-wishlistaddedbrowse,
				.yith-wcwl-wishlistexistsbrowse{
					position: absolute;
					left: 0;
					top: 0;

					&:before{
						content:"\f004";
						font-family: "FontAwesome";
						color: $primary-color;
					}

					a{
						position: absolute;
						overflow: hidden;
						text-indent: -9999px;
						width: 16px;
						height: 16px;
						top: 0;
						left: 0;
					}
					.feedback{
						position: absolute;
						background-color: rgba(0,0,0,.7);
						padding: 3px 5px;
						color: white;
						bottom: 100%;
						margin-bottom: 10px;
						right: -5px;
						border-radius: 3px;
						@include em(12);
						text-transform: initial;
						letter-spacing: normal;
						min-width: 150px;
						font-family: $secondary-font;
						opacity: 0;
						transform: scale(0);
						-webkit-transform: scale(0);
						transform-origin: 100% 100%;
						-webkit-transform-origin: 100% 100%;
						transition: .3s ease;

						&:after{
							content:" ";
							width: 0;
							border-style: solid;
							border-width: 5px;
							border-color: rgba(0,0,0,.7) transparent transparent transparent;
							position: absolute;
							top: 100%;
							right: 10px;
						}
					}

					&:hover .feedback{
						opacity: 1;
						transform: scale(1);
						-webkkit-transform: scale(1);
					}
				}
			}
		}
	}

	&:hover .product-action{
		opacity: 1;
		transform: scale(1);
		-webkit-transform: scale(1);

		.detail{
			transform: translateY(0);
			-webkit-transform: translateY(0);
			opacity: 1;
		}

		.plus{
			&:before, &:after{ 
				opacity: 1;
			}
			&:after{
				transform: rotate(90deg);
				-webkit-transform: rotate(90deg);
			}
			&:before{
				transform: rotate(0);
				-webkit-transform: rotate(0);
			}
		}
	}
}

@-webkit-keyframes rotate{
	from{ 
		transform: rotate(0deg);
		-webkit-transform: rotate(0deg);
	}
	to{ 
		transform: rotate(360deg);
		-webkit-transform: rotate(360deg);
	}
}
@-moz-keyframes rotate{
	from{ transform: rotate(0deg) }
	to{ transform: rotate(360deg) }
}
@keyframes rotate{
	from{ transform: rotate(0deg) }
	to{ transform: rotate(360deg) }
}

.product-category{
	.featured-image{
		transition: .3s ease;
		background-position-y:0!important;
	}
	.product-detail{
		@include cover;
		background-color: rgba(50,50,50,.3)!important;
		transition: .3s ease;
		z-index: 1;

		&:before{
			content:" ";
			width: 50%;
			height: 100%;
			position: absolute;
			background-color: rgba(255,255,255,.1);
			-webkit-transform: skew(15deg) translateX(-250%);
			transform: skew(15deg) translateX(-250%);
			transition: .3s ease-in;
			top: 0;
		}
	}

	&:hover {
		.product-detail{
			background-color: rgba(50,50,50,.5);

			&:before{
				transform: skew(15deg) translateX(150%);
				-webkit-transform: skew(15deg) translateX(150%);
				opacity: 0;
			}
		}
		.featured-image{
			transform: scale(1.1);
			-webkit-transform: scale(1.1);
		}
	}
	.product-data{
		text-align: center;
		color: white;
	}
	.product-category-name{
		@include em(24);
		margin: 0;
		letter-spacing: 2px;
		text-transform: uppercase;
		font-weight: 500;
		display: inline-block;
		position: relative;

		.count{
			position: absolute;
			top: -20px;
			right: -20px;
			font-size: 12px;
			color: white;
			background-color: $primary-color;
			border-radius: 30px;
			height: 30px;
			padding: 7px 10px;
		}
	}
	.product-category-desc{
		display: block;
		letter-spacing: 1px;
		text-transform: uppercase;
		margin-bottom: 40px;
	}
}

.woocommerce-message{
	position: fixed;
	width: 100%;
	top: 0;
	left: 0;
	padding: 20px 0 10px;
	background-color: white;
	z-index: 9;
	border-bottom: 2px solid $primary-color;
	transform: translateY(-100px);
	-webkit-transform: translateY(-100px);
	transition: .3s ease;

	&.visible{
		transform: translateY(0);
		-webkit-transform: translateY(0);
	}

	.admin-bar &{
		top: 32px;
	}

	a{
		margin-top: -10px;
		float: right;
	}
}

.woocommerce-error{
	margin: 0 0 30px!important;
	padding: 30px;
	background-color: #F5B9B9;
	border: 1px solid #BE6A6A;
	list-style: none;

}

// Single
.product-overview{
	background-color: white;
	margin-bottom: 10px;
	padding: 60px 100px 100px;
	position: relative;

	@media screen and (max-width:640px){ padding: 30px;}

	.onsale{
		position: absolute;
		left: 100px;
		top: 60px;
		background: $primary-color;
		text-transform: uppercase;
		padding: 3px 10px;
		@include em(12);
		font-weight: 700;
		font-family: $secondary-font;
		color: white;
		z-index: 1;

		&.soldout{
			width: 100px!important;
		}
	}
	
	.images{
		@extend .col-md-6;
		position: relative;

		.zoom{
			display: block;
		}

		> a {
			img{
				max-width: 100%;
				height: auto;
			}
		}

		.thumbnails{
			overflow: hidden;
			margin-bottom: 30px;
			img {
				// border: 3px solid rgba(90,90,90,.1);
				width: 100%;
				max-width: 100%;
				height: auto;
				transition: .3s ease;

				&:hover{
					opacity: 1!important;
					border-color: rgba(0,0,0,.4);
				}
			}

			.owl-item.active img{
				opacity: 1!important;
				border-color: rgba(0,0,0,.4);
			}

			.owl-prev,.owl-next{
				width: 40px;
				position: absolute;
				top: 0;
				bottom: 0;
				background: rgba(255,255,255,.7);
				text-align: center;
				@include em(24);
				transition: .3s ease;

				.fa{
					position: absolute;
					top: 50%;
					width: 100%;
					text-align: center;
					transform: translateY(-50%);
					-webkit-transform: translateY(-50%);
					left: 0;
					color: #333
				}
			}
			.owl-prev{ 
				left: 0;
				background: linear-gradient(90deg, rgba(255,255,255,.9), rgba(255,255,255,0));
				transform:translateX(-40px);
				-webkit-transform:translateX(-40px);
			}
			.owl-next{ 
				right: 0;
				background: linear-gradient(90deg, rgba(255,255,255,0), rgba(255,255,255,.9));
				transform:translateX(40px);
				-webkit-transform:translateX(40px);
			}

			&:hover{
				img{
					opacity: .5;
				}

				.owl-prev,.owl-next{
					transform: translateX(0);
					-webkit-transform: translateX(0);
				}
			}
		}

	}
	.product-summary{
		@extend .col-md-5;
		@extend .col-md-offset-1;
		
		.posted_in{
			@include em(14);
			font-style: italic;
		}

		.price{
			@include em(24);
			padding-bottom: 20px;
			margin-bottom: 20px;
			border-bottom: 1px dashed $muted-color;

			del{
				color: $muted-color;
			}
			ins{ text-decoration: none; }
		}
		
		.cart{
			padding-bottom: 20px;
			margin-bottom: 20px;
			border-bottom: 1px dashed $muted-color;
		}
		.quantity{
			margin-bottom: 10px;
			label{
				margin-right: 20px;
				display: inline-block;
			}
		}

		.summary{
			line-height: 1.7;

		}

	}
	.product_meta{
		margin-top: 0;

		>span{
			display: block;
			// margin-bottom: 10px;

			strong{
				color: $strong-color;
				font-style: normal;
			}
		}
	}
}

.product-details{
	padding: 50px 100px;
	background-color: white;
	margin-bottom: 10px;

	@media screen and (max-width:640px){ padding: 30px;}

	.shop_attributes{
		p:last-child{
			margin-bottom: 0;
		}
	}
}

.woocommerce-tabs{
	overflow: hidden;
	.tabs{
		@extend .col-md-3;
		list-style: none;
		margin: 0;

		@media screen and (max-width:990px){
			margin-bottom: 50px;
		}

		li{
			border-left: 2px solid transparent;

			&.active{
				border-left-color: $secondary-color;
			}
		}

		a{
			padding: 10px 15px;
			display: block;
			text-transform: uppercase;
			letter-spacing: 2px;
			@include em(11);
		}
	}

	.panel{
		@extend .col-md-9;
		float: right;

		@media screen and (max-width: 990px){
			float: none;
			width: 100%;
		}
	}

	.entry-content{
		h1,h2,h3,h4,h5,h6{
			font-weight: 300;
		}
	}
}

#reviews{

	@media screen and (max-width: 990px){
		.comment {
			padding-left: 80px!important;
		}
		
	}
}

#review_form{
	.comment-form-author, .comment-form-email{
		width: 50%;
	}
}

.related-title{
	text-align: center;
	background-color: white;
	margin-bottom: 10px;
	padding: 50px;
	text-transform: uppercase;
	letter-spacing: 2px;
}

// Rating
.star-rating {
	float: right;
	overflow: hidden;
	position: relative;
	height: 1em;
	line-height: 1;
	font-size: 1em;
	width: 5.4em;
	font-family: "FontAwesome";
	color: $primary-color;

	&:before {
		content: "\f006\f006\f006\f006\f006";
		float: left;
		top: 0;
		left: 0;
		position: absolute
	}

	span {
		overflow: hidden;
		float: left;
		top: 0;
		left: 0;
		position: absolute;
		padding-top: 1.5em;

		&:before {
			content: "\f005\f005\f005\f005\f005";
			top: 0;
			position: absolute;
			left: 0
		}
	}
}

.woocommerce-product-rating {
	line-height: 2;
	display: block;

	&:after,&:before {
		content: " ";
		display: table
	}

	&:after {
		clear: both
	}

	.star-rating {
		margin: .5em 4px 0 0;
		float: left
	}
}

p.stars {
	position: relative;
	font-size: 1em;

	a {
		display: inline-block;
		font-weight: 700;
		margin-right: 1em;
		text-indent: -9999px;
		position: relative;
		border-bottom: 0!important;
		outline: 0;

		&:last-child {
			border-right: 0
		}

		&.star-1,&.star-2,&.star-3,&.star-4,&.star-5 {
			border-right: 1px solid #ccc
		}

		&.star-1:after,&.star-2:after,&.star-3:after,&.star-4:after,&.star-5:after {
			font-family: "FontAwesome";
			text-indent: 0;
			position: absolute;
			top: 0;
			left: 0
		}

		&.star-1 {
			width: 2em
		}

		&.star-1:after {
			content: "\f006"
		}

		&.star-1.active:after,&.star-1:hover:after {
			content: "\f005"
		}

		&.star-2 {
			width: 3em
		}

		&.star-2:after {
			content: "\f006\f006"
		}

		&.star-2.active:after,&.star-2:hover:after {
			content: "\f005\f005"
		}

		&.star-3 {
			width: 4em
		}

		&.star-3:after {
			content: "\f006\f006\f006"
		}

		&.star-3.active:after,&.star-3:hover:after {
			content: "\f005\f005\f005"
		}

		&.star-4 {
			width: 5em
		}

		&.star-4:after {
			content: "\f006\f006\f006\f006"
		}

		&.star-4.active:after,&.star-4:hover:after {
			content: "\f005\f005\f005\f005"
		}

		&.star-5 {
			width: 6em;
			border: 0
		}

		&.star-5:after {
			content: "\f006\f006\f006\f006\f006"
		}

		&.star-5.active:after,&.star-5:hover:after {
			content: "\f005\f005\f005\f005\f005"
		}
	}


}

// Woo Column
.col2-set{
	width: 100%;
	&:after{
			content: "";
			display: block;
			clear: both
	}
	.col-1{
			float: left;
			width: 48%
	}
	.col-2{
			float: right;
			width: 48%
	}
	.col-1,.col-2{
		@media screen and (max-width: 768px){
			width: 100%;
			float: none;
		}
	}
}



// Woocommerce form
form {

	.form-row{
			padding: 3px;
			margin: 0 0 6px;

		&:after{
				content: "";
				display: block;
				clear: both
		}
		label{
				display: block
		}

		label.hidden{
				visibility: hidden
		}

		select{
				width: 100%;
				cursor: pointer;
				margin: 0
		}

		.required{
				color: red;
