.classic-style{
	margin: 30px 0;
	display: flex;
	flex-flow: row wrap;
	list-style: none;
	padding: 0;

	.product{
		position: relative;
		float: left;
		width: 25%;
		padding: 5px;
		display: flex;

		@media screen and (max-width: 990px){ width: 33.333%; }
		@media screen and (max-width: 640px){ width: 50%; }
		@media screen and (max-width: 480px){ 
			width: 100%;
			display: block;
		}

		&__inner{
			background-color: white;
			transition: box-shadow .3s ease;
			display: flex;
			width: 100%;
			flex-flow: column wrap;

			@media screen and (max-width: 480px){ 
				width: 100%;
				display: block;
			}
		}
		&:hover .product__inner{
			box-shadow: 0 2px 20px -3px rgba(0,0,0,.1);
		}

		&__image{
			display: block;
			position: relative;
			background-color: white;

			img{
				display: block;
				transition: .3s ease;
				width: 100%;
				height: auto;
				max-width: 100%;
			}

			.image{
				position: relative;
				display: block;

				&:after{
					content:" ";
					width: 100%;
					height: 100%;
					top:0;
					left: 0;
					background-color: rgba(255,255,255,.6);
					opacity: 0;
					position: absolute;
					transition: opacity .3s ease;
				}
			}

			.detail-circle{
				width: 200px;
				height: 200px;
				position: absolute;
				background-color: white;
				top: 0;
				bottom: 0;
				left: 0;
				right: 0;
				margin: auto;
				border-radius: 50%;
				display: flex;
				flex-flow: column wrap;
				align-items: center;
				justify-content: center;
				opacity: 0;
				transform: scale(.5);
				transition: .3s cubic-bezier(0.46, 0.1, 0.21, 1.69);
				will-change: opacity, transform;

				.simple-icon-magnifier{
					font-size: 36px;
				}
			}
		}

		&:hover{
			.image:after{
				opacity: 1;
			}
			.detail-circle{
				transform: scale(1);
				opacity: 1;
			}
			.action-addon{
				.yith-wcqv-button,.yith-wcwl-add-to-wishlist{
					opacity: 1;
					transform: translateY(0) rotate(0);
				}
				
			}

			.product__action{
				.price{
					transform: translateY(20px);
					opacity: 0;
				}
				.add_to_cart_button,
				.button.product_type_simple,
				.button.product_type_external,
				.button.product_type_grouped{
					pointer-events: initial;
					opacity: 1;
					transform: translateY(0);
				}
			}
		}

		.onsale{
			position: absolute;
			background-color: $primary-color;
			right: 0;
			top: 20px;
			color: white;
			padding: 5px 10px 3px;
			font-size: 12px;
			line-height: 1;
		}

		&__detail{
			padding: 20px;
			text-align: center;
			flex-grow: 1;
		}

		&__title{
			margin: 0 0 5px;
			font-size: 16px;
			line-height: normal;

			a{
				color: $strong-color;
			}
		}

		&__category{
			color: $muted-color;
			font-style: italic;
			font-size: 12px;
			margin-bottom: 10px;

			a{
				color: $muted-color;
			}
		}

		.star-rating{
			float: none;
			margin: 0 auto 10px;
		}

		.action-addon{
			position: absolute;
			bottom: 0;
			width: 100%;
			display: flex;
			justify-content: space-between;
			padding: 20px;
			overflow: hidden;

			.yith-wcqv-button,.yith-wcwl-add-to-wishlist{
				transform: translateY(50px) rotate(30deg);
				opacity: 0;
				will-change: opacity, transform;
				transition: .3s cubic-bezier(0.46, 0.1, 0.21, 1.69);
				position: relative;
			}

			.yith-wcqv-button{
				width: 40px;
				height: 40px;
				display: inline-block;
				text-align: center;
				background: $secondary-color;
				vertical-align: middle;
				color: white;
				border-radius: 50%;
				display: inline-flex;
				align-items: center;
				justify-content: center;
				margin: 0 2px;
				.tooltip{
					position: absolute;
					visibility: hidden;
				}
			}
			.yith-wcwl-add-to-wishlist{
				display: inline-block;
				vertical-align: middle;
				margin: 0 2px;
				position: relative;
				transition-delay: .3s ease;

				.add_to_wishlist{
					width: 40px;
					height: 40px;
					display: inline-flex;
					align-items: center;
					justify-content: center;
					background-color: pink;
					border-radius: 50%;
				}

				.fa{margin: 0;}

				img{
					position: absolute;
					left: 0;
					right: 0;
					top: 0;
					bottom: 0;
					margin: auto;
				}
			}
			.clear{display: none;}
		}

		&__action{
			height: 41px;
			overflow: visible;
			position: relative;

			.price{
				display: block;
				padding: 10px 15px;
				position: absolute;
				width: 100%;
				left: 0;
				top: 0;
				transition: .3s ease;
			}
			.add_to_cart_button,
			.button.product_type_simple,
			.button.product_type_external,
			.button.product_type_grouped{
				z-index: 1;
				vertical-align: middle;
				opacity: 0;
				pointer-events: none;
				transform: translateY(10px);
				transition: .3s ease;
			}
			.added_to_cart.wc-forward{display: none;}
			.yith-wcqv-button{display: none;}
			

		}
	}

}

