				display: inline-block;
				margin-right: 10px;
			}

		}
		&--dashboard a:before{ content:"\e6aa" }
		&--orders a:before{ content:"\e687" }
		&--edit-address a:before{ content:"\e61c" }
		&--edit-account a:before{ content:"\e61d" }
		&--downloads a:before{ content:"\e6a7" }
		&--customer-logout a:before{ content:"\e732" }

		&.is-active a,
		&:hover a{
			color: $primary-color;
			
			border-color: inherit;
		}
	}
		
}

