.button{
	text-transform: uppercase;
	display: inline-block;
	padding: 10px 15px;
	@include em(12);
	letter-spacing: 1px;
	border: 2px solid $secondary-color;
	background-color: transparent;
	color: $secondary-color;
	transition: .3s ease;
	cursor: pointer;
	border-radius: 0;

	&:hover{
		background-color: $secondary-color;
		color: white;
		box-shadow: 0 5px 7px -2px rgba(0,0,0,.4);
	}

	&--outline{
		border-color: white;
		color: white;

		&:hover{
			background-color: white;
			color: $strong-color;
		}
	}

	&--secondary{
		border-color: $strong-color;
		color: $strong-color;

		&:hover{
			background-color: $strong-color;
			color: white;
		}
	}
}