.page-contact{
	.contact-detail{
		.address,.phone{
			padding-left: 30px;
			position: relative;

			.fa{
				position: absolute;
				left: 0;
				top: 5px;
				@include em(18);
			}
		}
	}
	.contact-map{
		margin-bottom: 30px;
	}
	.row{
		margin: 0 -15px;

		[class*="col-"]{
			padding: 0 15px;
		}
	}
}

hr{
	border: none;
	border-top: 1px dashed #e3e3e3;
	margin: 30px 0;
}

.archive-wrap{
	h3{
		text-transform: uppercase;
		font-weight: 700;
		letter-spacing: 2px;
		font-weight: 400;
		font-family: $secondary-font;
	}
	.archive-list{
		list-style: none;
		margin: 0 0 30px;
		li{ position: relative }
		> li{
			a { 
				border-bottom: 1px solid #e3e3e3;
				padding: 10px 0;
				display: block;
			}
		}
		li span{
			position: absolute;
			margin-top: 10px;
			right: 0;
			top:0;
		}

		ul{
			border-left: 1px dashed #e3e3e3;
			border-top: 1px solid #e3e3e3;
			margin-top: -1px;
			margin-left: 20px!important;

			a{ padding-left: 10px }
		}
	}
}
.content{
	.koo-image-slider{margin-bottom: 30px;}
	
}


.woocommerce-wishlist.woocommerce-page{
	.social-share-holder{
		display: none;
	}
}


.gm-style img{
	max-width: none!important;
}