// Header Style
.site-header{
	@media screen and (max-width:990px){
		padding-top: 60px;
	}
}
.top-header{
	background-color: white;
	position: relative;
	text-align: center;
	padding: 30px 0;
	min-height: 135px;
	.container{
		display: flex;
		justify-content: center;
		align-items: center;
	}
}

.header-search-form{
	position: relative;
	overflow: hidden;

	@media screen and (max-width:990px){
		position: absolute;
		margin-top: 0;
		bottom:100%;
		margin-bottom: 10px;
		left: 10px;
	}

	i,input{ vertical-align: middle; }
	i { 
		@include em(24);
		margin-left: 1px;
	}
	input{
		border: none;
		padding: 10px 20px;
		font-style: italic;
		min-width: 200px;
		outline: none;
		border-bottom:1px solid transparent!important;
		transition: .3s ease;
		@include em(13);

		@media screen and (max-width:990px){
			background-color: transparent;
		}

		@media screen and (max-width:768px){
			width: 150px;
		}

		&:focus + span{
			left: 0;
		}
	}
	span{
		width: 100%;
		left: -300px;
		position: absolute;
		bottom: 0;
		border-top: 1px solid;
		transition: .3s ease;
	}
}

.branding {
	text-align: center;
	display: inline-block;
	color: inherit;
	margin: auto;
	max-width: 340px;

	.site-title {
		margin: 0;
		font-weight: 300;
		@include em(28);
	}

	img{
		max-height: 250px;
	}

	.site-description {
		text-transform: uppercase;
		display: inline-block;
		border-top: 1px solid #f2e7e7;
		padding-top: 10px;
		letter-spacing: 2px;
	}
}

.header-tools{
	position: relative;
	z-index: 1001;
	@include em(13);
	
	@media screen and (max-width:990px){
		position: absolute;
		right: 0;
		bottom: 100%;
		margin-bottom: 10px;
	}

	.mini-cart{ display: inline-block }
	button{
		border: none;
		background: none;
		font-family: $secondary-font;
	}
	i{ 
		@include em(24);
		vertical-align: middle;
	}
}
.top-header .header-search-form,
.top-header .header-tools{
  flex-basis: 300px;
}
.top-header .header-search-form{
  text-align: left;
}
.top-header .header-tools{
  text-align: right;
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
	
	.top-header{
		.container{
			display: table;
			vertical-align: middle;
		}
		.header-search-form,
		.branding,
		.header-tools{
			display: table-cell;
		}

		.header-search-form,
		.header-tools{
			width: 300px;
			max-width: 100%;
		}
	}

}

.main-navigation{
	border-bottom: 1px dashed #dcd0c7;
	position: relative;
	z-index: 1000;
	transition: background .3s ease;
	width: 100%;
	top:0;

	@media screen and (min-width:991px){
		&.sticky-menu.sticked{
			position: fixed;
			top: 0;
			width: 100%;
			background-color: white;
			z-index: 1002;

			.admin-bar &{ top: 32px }
		}
		
	}
}
.desktop-navigation{
	margin: 0 auto;
}

.user-menu{
	position: relative;
	display: inline-block;

	&__toggle{
		outline: none;
		font-family: $secondary-font;
		i{ margin-right: 5px; }

		@media screen and (max-width: 640px){
			span{ display: none }
		}
	}

	&__box{
		position: absolute;
		right: 0;
		top: 100%;
		box-shadow: 0 2px 5px rgba(0,0,0,.1);
		border-radius: 3px;
		background-color: white;
		z-index: 99;
		transition: .3s ease;
		-webkit-transform: scale(0);
		transform: scale(0);
		opacity: 0;
		-webkit-transform-origin: 100% 0;
		transform-origin: 100% 0;

		&.active{
			-webkit-transform: scale(1);
			transform: scale(1);
			opacity: 1;
		}

		&-login{
			@include clearfix;
			padding: 20px;
			min-width: 300px;

			h3{
				text-transform: uppercase;
				font-family: $secondary-font;
				@include em(14);
				letter-spacing: 2px;
				color: $secondary-color;
			}

			input[type="text"],
			input[type="password"]{
				display: block;
				width: 100%;
				margin-bottom: 10px;
			}
			input[type="submit"]{
				float: right;
			}
			.signup-link{
				float: left;
				font-style: italic;
				padding-top: 10px;
				text-decoration: underline;
			}
		}
	}

	&__list{
		list-style: none;
		text-align: left;
		padding: 10px 0;
		// min-width: 250px;

		ul{
			list-style: none;
		}

		a{
			padding: 10px 30px;
			transition: .3s ease;
			display: block;
			font-family: $secondary-font;
			color: $secondary-color;
			text-transform: uppercase;
			font-weight: 700;
			letter-spacing: 1px;
			white-space: nowrap;
			@include rem(10);

			> i {
				font-size: 14px;
				margin-right: 10px;
				width: 16px;
				transition: .3s ease;
			}

			&:hover{
				color: white;
				background-color: $primary-color;

				> i{ color: white; }
			}

			
		}
	}
}