.page-header{
	text-align: center;
	padding: 60px 0;

	.page-title{
		@include em(30);
		text-transform: uppercase;
		font-weight: 500;
		margin: 0;
		letter-spacing: 3px;
	}
}

.breadcrumbs{
	@include em(10);
	text-transform: uppercase;
}

.posts-navigation{
	@include clearfix;
	margin-bottom: 20px;
	
	.pagination{ padding-top: 10px; }

	@media screen and (max-width: 640px){
		.browse-bytag, .pagination{
			width: 100%;
			float: none;
			text-align: center;
			margin-bottom: 10px;
			padding: 0;
		}
	}
}

.browse-bytag{
	font-style: italic;
	@include em(12);
	padding-left: 50px;

	strong { 
		font-weight: normal;
		margin-right: 20px;
	}

	a {
		margin-right: 20px;
	}
	select{
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;
		border: none;
		background: none;
		color: $accent-color;
		font-style: italic;
		padding: 10px 40px 10px 20px;
		outline: none;
		border-radius: 0;
		border-bottom: 1px solid $accent-color;
	}
}

.pagination{
	@include em(12);
	font-style: italic;
	padding-right: 50px;
	.previous{ margin-right: 10px; }
	.next{ margin-left: 10px; }

	.page-info{
		margin: 0 10px;
		display: inline-block;
	}

	&.align-center{
		text-align: center;
		background-color: white;
		margin-bottom: 6px;
		padding: 30px 0;
	}
}

.posts-holder{
	
	&.card{

		.post{
			&.sticky .post-inner{
				border: 2px solid $primary-color;
			}

			&__detail{
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				background-color: rgba(20,20,20,.4);
				padding: 0 20px;
				transition: .3s ease;
				overflow: hidden;
				z-index: 1;

				&:after{
					content:" ";
					width: 80%;
					height: 100%;
					background-color: rgba(255,255,255,.1);
					position: absolute;
					left: 0;
					left: 0;
					-webkit-transform: skewX(-45deg) translateX(-500px);
					transform: skewX(-45deg) translateX(-500px);
					transition: .5s ease-out;
				}
			}

			&:hover{
				.post__detail{
					background-color: rgba(20,20,20,.6);

					&:after{
						-webkit-transform: skewX(-45deg) translateX(600px);
						transform: skewX(-45deg) translateX(600px);
						opacity: 0;
					}
				}
				.post__data .entry-date{
					margin-bottom: 40px;
				}
			}

			&__data{
				text-align: center;
				color: white;
				transition: .3s ease;

				.entry-title{
					margin: 0;
					font-weight: 500;
					@include em(18);
					max-height: 84px;
					overflow: hidden;
					word-break: break-word;
				}
				.entry-date{
					display: block;
					margin-bottom: 30px;
					transition: .3s ease;
					font-style: italic;
					@include em(12);
				}
			}
		}

		.featured-image{
			min-height: 280px;
		}
	}

	&--classic{
		.post-title{
			padding: 50px 0;
			text-align: center;

			.author{
				font-style: italic;
			}
		}
		.entry-title{
			@include em(30);
			text-transform: uppercase;
			font-weight: 500;
			margin: 0;
			letter-spacing: 3px;

			a{
				color: $secondary-color;
			}
		}

		.sticky .post-content{
			border: 2px solid $primary-color;
		}

		.featured-image{
			margin-bottom: 30px;
			img{
				display: block;
				text-align: center;
				margin: 0 auto;
				max-width: 100%;
			}
		}

		.more-link{
			font-style: italic;
		}
	}
}
.link-wrapper, .status-media{
	padding: 50px 30px;
	text-align: center;
	background-size: cover;
	position: relative;
	z-index: 1;
	margin-bottom: 30px;

	&:before{
		content:" ";
		position: absolute;
		z-index: -1;
		width: 100%;
		height: 100%;
		top:0;
		left: 0;
		background-color: rgba(0,0,0,.3);
	}

	+ .more-link{ display: none }

	i[class*="simple"]{
		@include em(24);
		width: 72px;
		height: 72px;
		border-radius: 50%;
		border: 2px solid rgba(255,255,255,.9);
		display: inline-block;
		color: white;
		margin-bottom: 30px;
		line-height: 3;
	}

	.entry-title{
		text-transform: initial;
		letter-spacing: 0;
		a{
			color: white;
		}
	}

	.status-content{
		color: white;
	}

	iframe{
		display: table!important;
		margin: 0 auto!important;
		max-width: 80%;
	}

}
.status-media{

}
.post-content{
	background-color: white;
	padding: 100px;
	margin-bottom: 6px;
	position: relative;
	@extend .row;

	@media screen and (max-width: 768px){
		padding-left: 30px;
	}
	
	@media screen and (max-width: 640px){
		padding-right: 30px;
	}
	.post-meta-holder{
		@extend .col-md-3;
		position: static;
		
	}
	.post-meta{
		border-top: 2px solid;
		padding-top: 20px;

		&.stick{
			position: fixed;
			top: 20px;

			.has-sticky-menu &{
				top: 72;
			}

			.admin-bar &{
				top: 52px;
			}
			.admin-bar.has-sticky-menu &{
				top: 104px;
			}

			@media screen and (max-width: 990px){
				position: relative;
				top: 0
			}
		}
		&.stop-foot{
			position: absolute;
			top: auto!important;
			bottom: 100px;
		}

		.entry-date{
			@include em(16);
			margin-bottom: 30px;
			font-weight: 300;
		}
		
		.entry-taxonomy, .related{
			padding-left: 30px;
			position: relative;
			margin-bottom: 30px;

			h3{
				text-transform: uppercase;
				@include rem(11);
				font-weight: 300;
				letter-spacing: 2px;
				word-break: break-word;

				[class*="-icon-"]{
					position: absolute;
					@include rem(18);
					left: 0;
					top: -5px;
				}
			}
		}
		.entry-taxonomy{
			@include em(12);
			a{
				// text-transform: uppercase;
				font-style: italic;
				font-weight: 500;
			}
		}
		.related{
			ul{
				list-style: none;
			}
			li{
				margin-bottom: 20px;
			}
			.entry-title{
				@include rem(12);
				margin: 0;
				word-break: break-word;
				font-family: $secondary-font;
			}
			.entry-date{
				@include em(11);
				font-style: italic;
			}
		}
	}
	.content{
		@extend .col-md-9;
		padding-left: 40px;
		@media screen and (max-width: 768px){
			padding-left: 0;
			border-top: 2px solid;
			padding-top: 30px;
		}

		.type-page &{
			@extend .col-md-12;
		}

		.featured-image{
			margin-bottom: 30px;
			img,iframe{
				max-width: 100%;
				width: 100%;
			}
			img{ height: auto }
		}
	}
}

.post-author{
	position: relative;
	padding-left: 80px;
	padding-top: 40px;
	min-height: 90px;
	margin: 50px 0;

	&:before{
		content: " ";
		width: 50px;
		height: 2px;
		background-color: #e3e3e3;
		position: absolute;
		left: 0;
		top: 0;
	}

	&__image{
		position: absolute;
		left: 0;
		top: 40;
		width: 60px;
		height: 60px;

		img{
			border-radius: 50%;
			width: 100%;
			max-width: 100%;
		}
	}

	p{
		line-height: 1.7;
	}

	&__name{
		margin: 0 0 1em;
		a{
			color: $secondary-color;
		}
	}
}

.post-navigation{
	overflow: hidden;
	padding: 30px 0;
	margin: 50px 0;
	position: relative;

	&:before, &:after{
		content:" ";
		width: 50px;
		border-top: 2px solid #e3e3e3;
		position: absolute;
		left: 0;
	}

	&:before{ top: 0 }
	&:after{ bottom: 0 }

	.previous-post,.next-post{
		width: 50%;
		float: left;
		@include em(15);
		font-weight: 300;
		line-height: 1.5;
		font-family: $secondary-font;
		font-weight: 700;

		@media screen and (max-width: 768px){
			width: 100%;
			padding: 0!important;
			text-align: left!important;
			margin-bottom: 30px;
		}
	}
	.previous-post{ padding-right: 15px; }
	.next-post{ 
		text-align: right;
		padding-left: 15px;
		float: right;
	}
}

.social-share-holder{
	position: absolute;
	right: 0;
	width: 50px;
	top: 100px;
	bottom: 0;
}
.social-share{
	border-top: 2px solid;
	width: 50px;

	&.stick{
		position: fixed;
		top: 20px;
		
		.has-sticky-menu &{
			top: 72;
		}
		.admin-bar &{
			top: 52px;
		}
		.admin-bar.has-sticky-menu &{
			top: 104px;
		}

		@media screen and (max-width: 990px){
			position: relative;
			top: 0
		}
	}
	&.stop-foot{
		position: absolute;
		top: auto!important;
		bottom: 100px;
	}

	@media screen and (max-width: 640px){
		position: relative;
		width: 100%;
		top: 0;
	}

	&.social-links{
		a{
			display: block;
			width: 100%;
			padding: 15px 0;
			line-height: 1;
			height: auto;

			@media screen and (max-width: 640px){
				display: inline-block;
				width: 48px;
			}
		}
	}
}

// .comments-area{

.comments-title,
.comment-reply-title{
	text-transform: uppercase;
	font-weight: 300;
	letter-spacing: 2px;
	@include em(14);
	margin-bottom: 50px;
}

.comment-list, .commentlist, .children{
	list-style: none;
	margin-left: 0!important;
}

.comment{
	padding-left: 80px;
	position: relative;
	margin-bottom: 50px;

	@media screen and (max-width: 990px){
		padding-left: 0;

		footer{
			padding-left: 80px;
			position: relative;
			min-height: 60px;

		}
	}

	&.bypostauthor{
		footer img{
			border: 3px solid $primary-color;
		}
	}

	.says{ display: none }

	.avatar{
		position: absolute;
		left: 0;
		top: 0;
		border-radius: 50%;
	}
	.fn{ 
		@include em(14);
		margin-bottom: 0;
	}
	.comment-metadata{ line-height: 1.3; }
	time,.edit-link{ 
		@include em(12);
		margin-right: 10px;
	}
	.comment-meta{
		margin-bottom: 30px;
	}
	.comment-body{
		margin-bottom: 50px;
	}
	.reply{
		position: relative;

		&:after{
			content:" ";
			width: 50px;
			border-top: 3px solid #e3e3e3;
			position: absolute;
			bottom: -25px;
			left: 0;

		}
	}
}

.comment-respond{
	margin-top: 100px;
	
	.comment-form{
		margin: 0 -5px;
	}
	.comment-form-author,
	.comment-form-email,
	.comment-form-url{
		width: 33.333%;
		padding: 0 5px;
		float: left;

		@media screen and (max-width: 768px){
			width: 100%;
			float: none;
		}
		label{
			@include em(12);
			margin-bottom: 5px;
			display: block;
			line-height: 1;
		}
		input{
			width: 100%;
		}
	}
	.comment-form-rating,.stars{
		padding: 0 3px;
	}
	.comment-form-comment{
		padding: 0 5px;
		label{
			@include em(12);
			margin-bottom: 5px;
			display: block;
			line-height: 1;
		}
		textarea{
			width: 100%;
			min-height: 150px;
			resize: vertical;
		}
	}
	.form-allowed-tags{ 
		@include em(12);
		padding: 0 5px;
		color: $muted-color;
	}
	.form-submit{
		padding: 0 5px;
		.submit{
			border: 1px solid;
			padding: 10px 20px;
			text-transform: uppercase;
			background: none;
		}
	}
}
.entry-content,.page-content,.comment-content, .entry-summary{
	line-height: 1.8;
	word-break: break-word;

	p{
		word-break: break-word;
	}

	.leading{
		@include em(18);
		font-weight: 300;
		margin-bottom: 50px;
	}

	ul,ol{
		margin-left: 20px;
	}

	img{
		max-width: 100%;
		height: auto;
	}

	table{
		margin-bottom: 30px;
		tr{
			th,td{
				padding: 10px;
			}
			th{
				background-color: $secondary-color;
				color: white;
			}
			td{
				border-bottom: 1px solid #e3e3e3;
			}
		}
		tbody{
			th{
				background: none;
				border-bottom: 1px solid #e3e3e3;
				color: $strong-color;
				text-align: left;
			}
		}
	}

	code{
		padding: 2px 3px;
		border-radius: 3px;
		background-color: #f9f6f4;
		box-shadow: 0 1px 1px rgba(0,0,0,.1);
	}
	pre{
		padding: 10px;
		border-radius: 3px;
		background-color: #f9f6f4;
	}
}

.gallery-slider{
	overflow: hidden;
	position: relative;
	margin-bottom: 30px;

	.slides{
		list-style: none;
		width: 2000%;
		margin: 0;

		li{
			float: left;
			width: 666px;

			img{
				width: 100%;
				display: block;
				height: auto;
			}
		}
	}

	.flex-direction-nav{
		list-style: none;
		margin: 0;

		.flex-prev, .flex-next{
			position: absolute;
			text-align: center;
			width: 40px;
			height: 50px;
			background-color: rgba(255,255,255,.7);
			top: 0;
			bottom: 0;
			margin: auto;
			color: $strong-color;
			transition: .3s ease;
			@include em(36);
		}
		.flex-prev{
			left: 0;
		}
		.flex-next{
			right: 0;
		}
	}

}

.gallery{
	&.gallery-columns-4{
		.gallery-item{
			width: 25%;
			&:nth-child(4n+1){ clear: both }
		}
	}
	&.gallery-columns-3{
		.gallery-item{
			width: 33.3333%;
			&:nth-child(3n+1){ clear: both }
		}
	}

	&-item{
		float: left;
		padding: 5px;

		img{
			width: 100%;
			max-width: 100%;
			display: block;
		}
	}

	&-caption{
		font-style: italic;
	}
}