$soundcloud : #FF4700;

.social-links{
	@include clearfix;
	line-height: normal;

	&.large{
		font-size: 30px;

		a{
			width: 40px;
			height: 40px;
			display: inline-block;
			padding: 0;
			text-align: center;
		}
	}
	&.medium{
		font-size: 24px;

		a{
			width: 40px;
			height: 40px;
			display: inline-block;
			padding: 0;
			text-align: center;
		}
	}
	&.small{
		font-size: 14px;
		a{
			width: 20px;
			height: 20px;
			display: inline-block;
			padding: 0;
			text-align: center;
		}
	}
	
	text-align: center;

	a{
		display: inline-block;
		padding: 0 20px;
		transition: .3s ease;
		border: none;

		
		&.facebook 		{ color: $facebook    }
		&.twitter 		{ color: $twitter     }
		&.google-plus 	{ color: $google-plus }
