		&.spotify 		{ color: $spotify     }
		&.codepen 		{ color: $codepen     }
		&.digg 			{ color: $digg        }
		&.foursquare 	{ color: $foursquare  }
		&.github 		{ color: $github      }
		&.reddit 		{ color: $reddit      }
		&.skype 		{ color: $skype       }
		&.behance 		{ color: $behance     }
		&.youtube 		{ color: $youtube     }
		&.steam 		{ color: $steam       }
		&.dribbble 		{ color: $dribbble    }
		&.tumblr 		{ color: $tumblr      }
		&.wordpress 	{ color: $wordpress   }
		&.instagram 	{ color: $instagram   }
		&.pinterest 	{ color: $pinterest   }
		&.flickr 		{ color: $flickr      }
		&.vimeo 		{ color: $vimeo       }
		&.vine 			{ color: $vine        }
		&.deviantart 	{ color: $deviantart  }
		&.lastfm 		{ color: $lastfm      }
		&.soundcloud 	{ color: $soundcloud  }


		&:hover{ color: #adadad }
	}

