.vc_general.vc_btn3,
.vc_btn{
	font-family: $secondary-font;
	text-transform: uppercase;
	list-style: 1px;
}

.has-overlay{ 
	position: relative; 
	z-index: 1;

	.tokoo-row-overlay{
		display: none;

		&:nth-of-type(1){
			display: block;
		}
	}

	.vc_video-bg{
		z-index: -10!important;
	}
	
}

// VC Row Style
.vc_row.bg-top-left, .vc_column_container.bg-top-left          { background-position : top left     !important; }
.vc_row.bg-top-center, .vc_column_container.bg-top-center      { background-position : top center   !important; }
.vc_row.bg-top-right, .vc_column_container.bg-top-right        { background-position : top right    !important; }
.vc_row.bg-center-left, .vc_column_container.bg-center-left    { background-position : center left  !important; }
.vc_row.bg-center, .vc_column_container.bg-center              { background-position : center       !important;} 
.vc_row.bg-center-right, .vc_column_container.bg-center-right  { background-position : center right !important; }
.vc_row.bg-bottom-left, .vc_column_container.bg-bottom-left    { background-position : bottom left  !important; }
.vc_row.bg-bottom-center, .vc_column_container.bg-bottom-center{ background-position : bottom center!important; }
.vc_row.bg-bottom-right, .vc_column_container.bg-bottom-right  { background-position : bottom right !important; }

.fixed-background {
	background-attachment: fixed!important;
	.vc_parallax-inner {
		background-attachment: fixed!important;
	}
}

.video-background{
	position: relative;
	overflow: hidden;
	z-index: 1;
}

.tokoo-row-overlay{
	@include cover;
	position: absolute!important;
	z-index: -9;
}

.video-bg-wrapper{
	position: absolute;
	top: 0;
	bottom: 0;
	width: 100%;
	overflow: hidden;
	z-index: -10;

	video{
		width: auto;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		position: absolute;
		margin: auto;
		min-width:100%;
		min-height: 100%;
	}
}


.vc_grid-filter-item{
	font-family: $primary-font;
	font-style: italic;
	@include em(12);

	&.vc_active,&:hover{
		background-color: transparent!important;
		color: $primary-color!important;
		span{ color: $primary-color!important; }
	}

}

.koo-icon-box{
	@include clearfix;
	text-align: center;
	width: 100%;
	
	&__icon{
		font-size:18px;
		margin-bottom: 20px;
		
		&--circle{
			width: 100px;
			height: 100px;
			border-radius: 50%;
			color: white;
			display: flex;
			margin: 0 auto 20px;
			
			.fa, [class*="ico"]{ margin: auto }
		}
		
		&--circle-outline{
			width: 100px;
			height: 100px;
			border: 2px solid;
			border-radius: 50%;
			display: flex;
			margin: 0 auto 20px;
			
			.fa, [class*="ico"]{ margin: auto }
		}
	}
	
	&--left-icon{
		position: relative;
		text-align: left;
		
		.koo-icon-box__icon{
			width: 64px;
			height: 64px;
			float: left;
			margin-right: 30px;
		}
	}
	
	&--right-icon{
		position: relative;
		text-align: left;
		
		.koo-icon-box__icon{
			float: right;
			margin-left: 30px;
			width: 64px;
			height: 64px;
		}
	}

	&__content{
		display: table;
	}
	
	
	.text-align-left{ text-align: left!important; }
	.text-align-right{ text-align: right!important; }
	
}