// WordPress Core Styling
.alignnone {
	margin: 5px 20px 20px 0;
}

.aligncenter,
div.aligncenter {
	display: block;
	margin: 5px auto 5px auto;
}

.alignright {
	float:right;
	margin: 5px 0 20px 20px;
}

.alignleft {
	float: left;
	margin: 5px 20px 20px 0;
}

a img.alignright {
	float: right;
	margin: 5px 0 20px 20px;
}

a img.alignnone {
	margin: 5px 20px 20px 0;
}

a img.alignleft {
	float: left;
	margin: 5px 20px 20px 0;
}

a img.aligncenter {
	display: block;
	margin-left: auto;
	margin-right: auto
}

.wp-caption {
	background: #fff;
	max-width: 100%; 
	text-align: center;
	position: relative;

	&.alignnone {
		margin: 5px 20px 20px 0;
	}
	&.alignleft {
		margin: 5px 20px 20px 0;
	}

	&.alignright {
		margin: 5px 0 20px 20px;
	}

	img {
		border: 0 none;
		height: auto;
		margin: 0 auto;
		max-width: 98.5%;
		padding: 0;
		width: auto;
		display: block;
	}

	.wp-caption-text {
		@include em(12);
		margin: 0;
		padding: 10px;
		position: absolute;
		background-color: rgba(255,255,255,.3);
		bottom: 0;
		width: 100%;
		text-align: left;
		color: white;
	}

}