// Footer Style
.site-footer{
	background-color: white;
	margin-top: 50px;
}
.widget-area{
	display: table;
	width: 100%;
	padding: 40px 0;
	table-layout: fixed;

	[class*="widget-area__footer-"]{
		display: table-cell;
		vertical-align: top;
		padding: 20px 50px;

		@media screen and (max-width: 990px){
			width: 100%;
			display: block;
			border-right: none!important;

			&:not(:last-child){border-bottom: 1px dashed rgba(0,0,0,.2);}
		}

		&:not(:last-child){
			border-right: 1px dashed rgba(0,0,0,.2);
		}
	}

	a,.entry-title{
		word-break: break-word;
	}

	.widget-title{
		color: rgba(0,0,0,.3)
	}
}
.colophon{
	@include clearfix;
	padding: 40px 50px;
	border-top: 1px dashed rgba(0,0,0,.2);
	color: rgba(0,0,0,.6);


	a, strong{ color: rgba(0,0,0,.8) }
	
	.copy{ 
		@include em(11);
		font-style: italic;
	}
	
	&__left, &__right{ max-width: 50% }
	&__left{ float: left }
	&__right{ float: right }

	@media screen and (max-width: 640px){
		text-align: center;

		img{
			display: block;
			margin: 0 auto 10px;
			text-align: center;
		}
		
		&__left, &__right{ 
			float: none;
			max-width: 100%;
		}

	}
}