// Font family
$primary-font     : "Merriweather", "PT Serif","Droid Sans", serif;
$secondary-font   : "Raleway", "Roboto", "Open Sans", arial, sans-serif;


// Color Variables
$accent-color     : #d9534d;
$primary-color    : $accent-color;
$secondary-color  : #974844;

// Body text color
$bg-color         : #f9f6f4;

// typography color
$strong-color     : #3f3232;
$body-color       : #585757;;
$muted-color      : #b1b1b1;

// Grid system
$grid-columns     : 12 !default;
$grid-gutter-width: 30px !default;

$browser-context: 14; // Default


.customizer{
	position: fixed;
	left: -250px;
	top: 20%;
	width: 250px;
	background-color: white;
	filter: drop-shadow(0 2px 5px rgba(0,0,0,.3));
	z-index: 999;
	transition: .3s ease;

	&.active{
		left: 0;
	}

	.customizer-toggle{
		position: absolute;
		left: 100%;
		top: 0;
		background: white;
		border: none;
		padding: 10px;
		background-color: $primary-color;
		color: white;
		outline: none;
	}

	&__list{
		list-style: none;
		margin: 0;

		li{
			border-bottom: 1px solid rgba(0,0,0,.1);
			padding: 15px 20px;

			&:last-child{
				border-bottom: none;
			}

			strong{
				display: block;
				text-transform: uppercase;
				letter-spacing: 2px;
				font-family: $secondary-font;
				margin-bottom: 10px;
			}
			input,select{
				width: 100%;
			}
		}
	}
}