// Breakpoint for responsive menu 
// ==============================
// Define the size of screen for  menu's breakpoint to be collapsed into mobile version.

$menu-breakpoint : 990px;


.desktop-navigation{
	position: relative;
	z-index: 9;

	.menu{
		list-style: none;
		margin: 0;
		padding: 0;
		text-align: center;

		@media screen and (max-width: $menu-breakpoint){ display: none }

		> .menu-item{
			display: inline-block;

			&.menu-item-has-children > a:after{
				content:"\f107";
				font-family: "FontAwesome";
				display: inline-block;
				vertical-align: middle;
				margin-left: 10px;
			}
		}

		> .page_item{
			display: inline-block;

			&.page_item_has_children > a:after{
				content:"\f107";
				font-family: "FontAwesome";
				display: inline-block;
				vertical-align: middle;
				margin-left: 10px;
			}
		}

	}
	.menu-item,.page_item{
		position: relative;

		&:hover > .sub-menu,
		&:hover > .children{
			opacity: 1;
			-webkit-transform: scale(1);
			transform: scale(1);
		}

		a{
			padding: 10px 20px;
			display: block;
			text-decoration: none;
			text-transform: uppercase;
			font-weight: 700;
			@include em(11);
			color: #333;
			white-space: nowrap;
			letter-spacing: 1px;

			[class*="ico"]{
				color: $primary-color;
				margin-right: 5px;
				display: inline-block;
			}
		}

		> a{ padding: 20px }

		// Mega Menu Style
		&.mega-menu{

			> .sub-menu {
				background-repeat: no-repeat;
				background-position: bottom right;
				padding: 30px;

				&.with_bg_image{
					background-size: contain;
					padding-right: 200px;

					&.top-left,&.bottom-left{ padding-left: 200px }
					&.top-left{ background-position: top left }
					&.top-right{ background-position: top right }

					&.top-right,&.bottom-right{ padding-right: 200px }
					&.bottom-left{ background-position: bottom left }
					&.bottom-right{ background-position: bottom right }
				}

				// Mega Menu Column
				>.menu-item{
					min-width: 250px;
					max-width: 100%;
					display: table-cell;
					padding: 10px;
					border-bottom: none!important;
				}
				
				.menu-item{
					&:not(:last-child){ border-bottom: 1px solid rgba(70,70,70,.1); }
					&:hover{
						> a{
							background-color: transparent;
							color: $secondary-color;
						}
					}
					a{
						color: $body-color;
						padding: 10px;
						white-space: normal;
						line-height: 1.7;

						&:hover{
							background-color: $primary-color;
							color: white;

							> i { color: white; }
						}
					}
				}

				// Mega Menu Heading
				.menu-item-has-children {
					> a{
						font-weight: 700!important;
						border-bottom: 2px solid $secondary-color;
						padding: 15px 10px;
						text-transform: uppercase;
						margin-bottom: 10px;
						letter-spacing: 1px;
						@include em(12);

						&:hover{
							background-color: transparent;
							color: $strong-color; 
						}
					}
				}

				.sub-menu{
					position: relative;
					left: 0;
					box-shadow: none;
					-webkit-transform: scale(1);
					transform: scale(1);
					opacity: 1;
					padding: 0;
					background-color: transparent;
					top: 0;

					.menu-item-has-children{
						margin-top: 10px;
					}
					
				}	
			}

			&.mega-fullwidth{
				position: static;

				> .sub-menu{
					left: 0;
					right: 0;
				}
			}
		}
	}

	.sub-menu,.children{
		position: absolute;
		-webkit-transform: scale(0);
		transform: scale(0);
		opacity: 0;
		list-style: none;
		margin: 0;
		padding: 30px 0;
		left: 0;
		background-color: white;
		box-shadow: 0 0 3px rgba(0,0,0,.1);
		transition: .3s ease;
		-webkit-transform-origin: 50% 0;
		transform-origin: 50% 0;
		text-align: left;

		.menu-item,.page_item{
			float: none;

			a{
				padding: 10px 30px;
			}

			&:hover > a{
				color: white;

				[class*="ico"]{
					color: white;
				}
			}
		}

		.sub-menu,.children{
			left: 100%;
			top: -30px;
			-webkit-transform-origin: 0 0;
			transform-origin: 0 0;
		}
	}
}

/* Mobile Navigation */
.menu-toggle{
	display: none;
	border: none;
	background: transparent;
	padding: 20px 10px;
	color: #232323;
	outline: none;
	text-align: center;
	width: 100%;
	font-family: $secondary-font;
	text-transform: uppercase;
	letter-spacing: 2px;

	.fa{
		@include em(18);
		margin-right: 20px;
		vertical-align: middle;
	}

	@media screen and (min-width: $menu-breakpoint+1){ display: none!important }
	@media screen and (max-width: $menu-breakpoint){ display: block }
}

/* Mobile Accordion Menu style */
.mobile-navigation{
	clear: both;
	display: none;
	z-index: 9;
	padding-bottom: 30px;

	@media screen and (min-width: $menu-breakpoint+1){ display: none!important }

	.menu,.menu > ul{
		list-style: none;
		margin: 0;
		padding-left: 0;
		background: white;
		border: 1px solid #dcd0c7;

		.sub-menu,
		.children{
			list-style: none;
			position: relative;
			padding-left: 20px;
			display: none;
			border-top: 1px solid #dcd0c7;
			background-image: none!important;

			.menu-item,.page_item{
				border-left: 1px dashed #dcd0c7;
			}
		}
	}
	.menu-item, .page_item{
		position: relative;

		&:not( :last-child ){ border-bottom: 1px solid #dcd0c7 }
		

		a{
			display: block;
			color: inherit;
			padding: 15px 20px;
			@include em(13);
			line-height: 1.5;

			&:hover{ color: $accent-color }

			> i{
				margin-right: 5px;
				display: inline-block;
				transition: .3s ease;
			}
		}


		&.current-menu-item{
			a{
				color: $accent-color;
			}
		}

	}
	.menu-item-has-children,.page_item_has_children{
		position: relative;

		> button{ 
			font-family: "FontAwesome";
			position: absolute;
			right: 0;
			top: 0;
			width: 50px;
			background: white;
			padding: 15px 25px 15px 20px;
			text-align: center;
			border: none;
			border-left:1px solid #dcd0c7;
			cursor: pointer;
			border-radius: 0;
			outline: none;
			color: $strong-color;
			@include em(18);
			line-height: 1.1;
		}

		&.active{
			&:after{
				content:"\f106";
			}
		}
	}

	

}