.card{
	@include clearfix;
	margin: 0 -5px;

	&-item{
		width: 33.3333%;
		float: left;
		padding: 0 5px 10px;

		&:nth-child(3n+1){ clear: both }

		@media screen and (max-width: 990px){
			width: 50%;
			
			&:nth-child(3n+1){ clear: none }
			&:nth-child(2n+1){ clear: both }
		}

		@media screen and (max-width: 640px){
			width: 100%;
			
			&:nth-child(3n+1){ clear: none }
			&:nth-child(2n+1){ clear: none }
		}

	}

	&-inner{
		position: relative;
		overflow: hidden;
		background-color: white;
	}

	&-image-bg{
		background-color: #111;
		opacity: 0;
		background-size: cover;
		background-position: center;
		transition: .2s ease;
		height: 280px;
		margin-bottom: 0!important;

		&.loaded{
			opacity: 1;
		}
	}

	&-valigned-content{
		position: absolute;
		top: 50%;
		left: 0;
		right: 0;
		padding-left: 20px;
		padding-right: 20px;
		-webkit-transform: translateY(-50%);
		transform: translateY(-50%);
	}

	.entry-more,.button{
		color: white;
		border-color: white;

		&:hover{
			color: $strong-color;
		}
	}
	
}

.columns-6 .card-item{
	width: 16.666667%;
	&:nth-child(3n+1){ clear: none }
	&:nth-child(6n+1){ clear: both }

	@media screen and (max-width: 1200px){
		width: 33.3333%;
		
		&:nth-child(3n+1){ clear: both }
	}

	@media screen and (max-width: 990px){
		width: 50%;
		
		&:nth-child(3n+1){ clear: none }
		&:nth-child(2n+1){ clear: none }
	}

	@media screen and (max-width: 640px){
		width: 100%;
		
		&:nth-child(3n+1){ clear: none }
		&:nth-child(2n+1){ clear: none }
	}
}

