	}
}

.columns-4 .card-item{
	width: 25%;
	&:nth-child(3n+1){ clear: none }
	&:nth-child(4n+1){ clear: both }

	@media screen and (max-width: 990px){
		width: 50%;
		
		&:nth-child(3n+1){ clear: none }
		&:nth-child(2n+1){ clear: none }
	}

	@media screen and (max-width: 640px){
		width: 100%;
		
		&:nth-child(3n+1){ clear: none }
