.portfolio-holder{
	@include clearfix;
	margin: 0 -3px;
	position: relative;

	.portfolio{
		width: 25%;
		position: relative;
		padding: $grid-gutter-width / 2 !important;

		@media screen and (max-width: 990px) { width: 33.3333% }
		@media screen and (max-width: 768px)  { width: 50% }
		@media screen and (max-width: 640px) { width: 100% }

		&-inner{
			position: absolute;
			left: $grid-gutter-width / 2;
			right: $grid-gutter-width / 2;
			top: $grid-gutter-width / 2;
			bottom: $grid-gutter-width / 2;
		}

		.featured-image{
			position: absolute;
			width: 100%;
			height: 100%;

			&:before{
				content: " ";
				@include cover;
				background-color: rgba(0,0,0,.3);
				transition: .3s ease;
			}
		}

		&:hover .featured-image:before{
			background-color: rgba(0,0,0,.6);
		}

		&__detail{
			position: absolute;
			bottom: 0;
			left: 0;
			padding: 20px;

			a{
				color: white;
			}

			.entry-title{
				margin: 0;
				font-weight: 300;
			}

			.entry-category{
				color: white;
				font-style: italic;
			}
		}
	}

	&.columns-3 .portfolio{ 
		width: 33.3333%;
		
		&:nth-child(3n+1){ clear: both };
		
		@media screen and (max-width:768px){
			width: 50%;
		}

		@media screen and (max-width: 480px){
			width: 100%;
		}
	}

	&.columns-5 .portfolio{ 
		width: 20%;
		@media screen and (max-width: 990px){
			width: 33.3333%;
		}
		@media screen and (max-width:768px){
			width: 50%;
		}

		@media screen and (max-width: 480px){
			width: 100%;
		}
	}
	&.columns-6 .portfolio{ 
		width: 16.667%;
		@media screen and (max-width: 990px){
			width: 33.3333%;
		}
		@media screen and (max-width:768px){
			width: 50%;
		}

		@media screen and (max-width: 480px){
			width: 100%;
		}
	}
}