*{
	box-sizing: border-box;
	&:before,&:after{
		box-sizing: border-box;
	}
}
// Global Styles
html{
	background-color: $bg-color;
}
body {
	color: $body-color;
	font-family: $primary-font;
	font-size   : #{$browser-context}px;
	font-weight : 400;
	line-height : 1.5;
	overflow-x: hidden;

	@include clearfix;
}
.screen-reader-text{
	position: absolute;
	top: -9999px;
	left: -9999px;
	visibility: hidden;
}
a { 
	text-decoration: none;
	transition: .3s ease;
	word-break: break-word;
}

address { font-style: normal }

p{ margin-top: 0 }

ul,ol{
	padding-left: 0;
}

iframe{
	display: block;
	border: none;
}

img,iframe{
	max-width: 100%;
}

.site-content{
	transition: .3s ease;
	
	&.boxed{
		box-shadow: 0 0 20px rgba(0,0,0,.1);
		margin-top: 30px;
		margin-bottom: 30px;
		border: 1px solid #e3e3e3;
	}
}

@import "../components/form";